.index {
  --bg-color: #000;
}

.index {
  height: 100vh;
  background-color: var(--bg-color);
}
.tree {
  position: absolute;
  bottom: 0px;
  width: 40vh;
  left: 50%;
  transform: translateX(-50%);
}
.tree__img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
  user-select: none;
}
