.moon {
  --bgColor: #fee786;
  width: 10vh;
  height: 10vh;
  border-radius: 50%;
  position: absolute;
  right: 10px;
  top: 10px;
  background-color: var(--bgColor);
  animation: luminescence 8s linear infinite;
}

@keyframes luminescence {
  0% {
    filter: drop-shadow(0px 0px 5px var(--bgColor));
  }

  50% {
    filter: drop-shadow(0px 0px 25px var(--bgColor));
  }

  100% {
    filter: drop-shadow(0px 0px 5px var(--bgColor));
  }
}
